.App {
  text-align: center;
  padding: 16px;
  margin: 10px;
}
.Fetch{
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
  display: inline-block;
  padding: 16px;
  border: 1px solid black;
  -webkit-box-shadow: 2px 2px gray;
          box-shadow: 2px 2px gray;
  width: 50%;
}
.Card{
  text-align: center;
  display: inline-block;
  padding: 16px;
  margin: 10px;
  border: 1px solid #ccc;
  -webkit-box-shadow: 2px 2px #eee;
          box-shadow: 2px 2px #eee;
}
#confirmed{
  color: red;
}
#active{
  color: blue;
}
#recovered{
  color: green;
}
#deceased{
  color: rgb(87, 84, 84);
}
#confirmed:hover{
  color: white;
  background-color: tomato;
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
#active:hover{
  color:white;
  background-color: cornflowerblue;
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
#recovered:hover{
  color: white;
  background-color: rgb(76, 190, 76);
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}
#deceased:hover{
  background-color: grey;
  color: white;
  -webkit-box-shadow: 8px 8px rgb(207, 203, 203);
          box-shadow: 8px 8px rgb(207, 203, 203);
}



